import React, { Component } from 'react';
import pay from './payfor.png';
import reputation from './reputation.png';
import truck from './truck.png';

import './AboutPage.css';

class AboutPage extends Component {

    render() {
        return (
                <div className="about_block">
                    <div className="row">
                        <div className="col-7 company_info">
                            <div className="about_title"> </div>
                            <p className="about_info"> <b> </b>-   
                                  -,      
                                   .      5000  
                                   .      ,   ,
                                         .</p>
                            <p className="about_info">       
                                 .  ,   , 
                                 ,    .     
                                ,      ,    
                                   .        .
                                          
                                 ,      .  
                                      .  
                                      .</p>
                            <p className="about_info">     ,      .
                                         ,   
                                   .       
                                      .    . ,
                                .    ,  .</p>
                        </div>

                        <div className="col-4 icons_block">
                            <div className="icon_info">
                                <img className="about_icon" src={truck} height="30px" width="auto" alt="truck"/>
                                <div className="icon_title"></div>
                                <p>     <br/>
                                            <br/>
                                     ,    <br/>
                                        </p>
                            </div>

                            <div className="icon_info">
                                <img className="about_icon" src={pay} height="30px" width="auto" alt="pay"/>
                                <div className="icon_title"></div>
                                <p>     LiqPay. <br/>
                                       Visa/Mastercard, , <br/>
                                      </p>
                            </div>

                            <div className="icon_info">
                                <img className="about_icon" src={reputation} height="30px" width="auto" alt="reputation"/>
                                <div className="icon_title"></div>
                                <p>-      <br/>
                                   ,    -.<br/>
                                          <br/>
                                    </p>
                            </div>
                        </div>
                    </div>
                </div>
        );
    }
}

export default AboutPage;


