import React, { Component } from 'react';
import './BoxesBlock.css';

/**
 *   
 */

/***   */
class BoxItem extends Component {

    render() {
        const {
            boxClass,
            description,
            hidClass,
        } = this.props;

        return (
            <div className={`col-3 box ${boxClass}`}>
                <div className="bath-info">{description}</div>
                <div className={`btn btn-2 ${hidClass}`}></div>
            </div>
        )
    }
}

/***   */
class BoxesBlock extends Component {

    render() {
        return (
            <div className="row boxes">
                <BoxItem
                    boxClass="b1"
                    description="    "
                />
                <BoxItem
                    boxClass="b5"
                    hidClass = "hidden"
                />
                <BoxItem
                    boxClass="b3"
                    description="        "
                />
                <BoxItem
                    boxClass="b4"
                    hidClass = "hidden"
                />
                <BoxItem
                    boxClass="b2"
                    hidClass = "hidden"
                />
                <BoxItem
                    boxClass="b6"
                    description="        "

                />
                <BoxItem
                    boxClass="b7"
                    hidClass = "hidden"
                />
                <BoxItem
                    boxClass="b8"
                    description="     "
                />
            </div>
        )
    }
}

export default BoxesBlock;


